.navbar {
  background-color: white;
  width: 100%;
  height: 100px;
}

.navbar .container {
  width: 70%;
  height: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .container .navlinks ul {
  display: flex;
  flex-direction: row;
}

.navbar .container .navlinks ul li {
  margin: 0 1rem;
}

.navbar .container .navlinks ul li a {
  color: #333333;
  outline: none;
  transition: 0.2s all ease-in-out;
}

.navbar .container .navlinks ul li a.active {
  color: #28abb9;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
  display: none;
  pointer-events: none;
  opacity: 0;
  transition: 0.2s all ease-in-out;
}

.sidebar {
  background-color: #ffffff;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 20;
  height: 100%;
  width: 70%;
  padding: 3rem;
  transform-origin: right center;
  transform: scaleX(0);
  transition: 0.2s all ease-in-out;
}

.sidebar ul {
  display: flex;
  flex-direction: column;
}

.sidebar ul li{
  margin-bottom: 2rem;
}

.sidebar ul li a {
  color: #333333;
  outline: none;
  transition: 0.2s all ease-in-out;
  font-weight: bold;
  font-size: 1.5rem;
}

.sidebar ul li a.active {
  color: #28abb9;
}

.toggle{
  display: none;
  background-color: #28abb9;
  color: #ffffff;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  z-index: 30;
  position: absolute;
  top: 20px;
  right: 30px;
}


@media only screen and (max-width: 900px) {
  .navbar .container {
    width: 80%;
  }
}

@media only screen and (max-width: 610px) {
  .navbar .container .navlinks {
    display: none;
  }

  .sidebar{
    display: block;
  }

  .backdrop{
    display: block;
  }

  .sidebar.active{
    transform: scaleX(1);
  }

  .backdrop.visible{
    pointer-events: all;
    opacity: 1;
  }

  .toggle{
    display: block;
  }
}
