.waiting{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 20vh;
    text-align: center;
    opacity: 0.7;
}
.waiting img{
    width: 20%;
    margin-bottom: 2rem;
}

@media only screen and (max-width:750px){
    .waiting img{
        width: 30%;
    }
}
@media only screen and (max-width:400px){
    .waiting img{
        width: 45%;
    }
}