.showcase{
    width: 100%;
    height: 100%;
    padding-top: 20vh;
}

.showcase h1{
    margin-bottom: 0.25rem;
}

.showcase div{
    margin: 2rem 0;
}

.button{
    padding: 0.5rem 1.25rem;
    border-radius: 5px;
    display: inline-block;
    margin-right: 1rem;
    outline: none;
}

.button.solid{
    background-color: #28abb9;
    color: #ffffff;
    border: 1px solid #28abb9;
}

.button.outline{
    color: #28abb9;
    border: 2px solid #28abb9;
    background-color: transparent;
}